"use client"

import * as React from "react"
import DashboardLayout from "@/components/dashboard-layout"
import { Badge } from "@/components/ui/badge"
import { Button } from "@/components/ui/button"
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card"
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table"
import {
  Tabs,
  TabsContent,
  TabsList,
  TabsTrigger,
} from "@/components/ui/tabs"
import {
  ChevronLeft,
  ChevronRight,
  Search,
  Filter,
  Download,
} from "lucide-react"
import { Input } from "@/components/ui/input"
import { TransactionFilters, FilterValues } from "@/components/transaction-filters"

interface Transaction {
  IdTransacao: string
  Data: string
  Estabelecimento: string
  ValorPositivo: number
  TipoTransacao: string
  GRUPO: string
  SUBGRUPO: string
  TipoGasto: string
  origem_classificacao: string
  MesFatura: string
  banco_origem: string
  NomeCartao: string
}

interface PaginationInfo {
  page: number
  limit: number
  total: number
  totalPages: number
}

export default function TransactionsPage() {
  const [activeTab, setActiveTab] = React.useState("all")
  const [transactions, setTransactions] = React.useState<Transaction[]>([])
  const [pagination, setPagination] = React.useState<PaginationInfo>({
    page: 1,
    limit: 10,
    total: 0,
    totalPages: 0
  })
  const [loading, setLoading] = React.useState(true)
  const [searchTerm, setSearchTerm] = React.useState("")
  const [filtersOpen, setFiltersOpen] = React.useState(false)
  const [appliedFilters, setAppliedFilters] = React.useState<FilterValues>({})

  const fetchTransactions = async (type: string, page: number = 1, filters: FilterValues = {}) => {
    try {
      setLoading(true)
      const params = new URLSearchParams({
        type,
        page: page.toString(),
        limit: pagination.limit.toString(),
      })

      // Adicionar filtros  query
      if (filters.estabelecimento) params.append('estabelecimento', filters.estabelecimento)
      if (filters.grupo) params.append('grupo', filters.grupo)
      if (filters.subgrupo) params.append('subgrupo', filters.subgrupo)
      if (filters.tipoGasto) params.append('tipoGasto', filters.tipoGasto)
      if (filters.banco) params.append('banco', filters.banco)
      if (filters.mesInicio) params.append('mesInicio', filters.mesInicio)
      if (filters.mesFim) params.append('mesFim', filters.mesFim)

      const response = await fetch(`/api/transactions?${params.toString()}`)
      
      if (response.ok) {
        const data = await response.json()
        setTransactions(data.transactions)
        setPagination(data.pagination)
      }
    } catch (error) {
      console.error('Erro ao buscar transaes:', error)
    } finally {
      setLoading(false)
    }
  }

  React.useEffect(() => {
    fetchTransactions(activeTab, 1, appliedFilters)
  }, [activeTab])

  const handleTabChange = (value: string) => {
    setActiveTab(value)
  }

  const handlePageChange = (newPage: number) => {
    fetchTransactions(activeTab, newPage, appliedFilters)
  }

  const handleApplyFilters = (filters: FilterValues) => {
    setAppliedFilters(filters)
    fetchTransactions(activeTab, 1, filters)
  }

  const activeFiltersCount = Object.values(appliedFilters).filter(v => v).length

  const formatCurrency = (value: number) => {
    return new Intl.NumberFormat('pt-BR', {
      style: 'currency',
      currency: 'BRL'
    }).format(value)
  }

  const formatDate = (dateString: string) => {
    const date = new Date(dateString)
    return date.toLocaleDateString('pt-BR', {
      day: '2-digit',
      month: '2-digit',
      year: 'numeric'
    })
  }

  const getTransactionTypeBadge = (type: string) => {
    switch (type) {
      case 'Receitas':
        return <Badge className="bg-green-100 text-green-800 hover:bg-green-100">Receita</Badge>
      case 'Despesas':
        return <Badge className="bg-red-100 text-red-800 hover:bg-red-100">Despesa</Badge>
      case 'Carto de Crdito':
        return <Badge className="bg-blue-100 text-blue-800 hover:bg-blue-100">Carto</Badge>
      default:
        return <Badge variant="outline">{type}</Badge>
    }
  }

  const getOrigemBadge = (origem: string) => {
    if (origem?.includes('Manual')) {
      return <Badge variant="outline" className="bg-purple-50 text-purple-700">Manual</Badge>
    } else if (origem?.includes('IA') || origem?.includes('Auto')) {
      return <Badge variant="outline" className="bg-blue-50 text-blue-700">IA</Badge>
    }
    return <Badge variant="outline">{origem || 'N/A'}</Badge>
  }

  const filteredTransactions = transactions.filter(t =>
    t.Estabelecimento?.toLowerCase().includes(searchTerm.toLowerCase()) ||
    t.GRUPO?.toLowerCase().includes(searchTerm.toLowerCase()) ||
    t.SUBGRUPO?.toLowerCase().includes(searchTerm.toLowerCase())
  )

  return (
    <DashboardLayout>
      <div className="flex flex-1 flex-col gap-4">
        <div className="flex items-center justify-between">
          <div>
            <h1 className="text-2xl font-semibold">Transaes</h1>
            <p className="text-muted-foreground">
              Gerencie e visualize todas as suas transaes financeiras
            </p>
          </div>
          <div className="flex gap-2">
            <Button variant="outline" size="sm" onClick={() => setFiltersOpen(true)}>
              <Filter className="mr-2 h-4 w-4" />
              Filtros
              {activeFiltersCount > 0 && (
                <span className="ml-1 rounded-full bg-primary px-1.5 py-0.5 text-xs text-primary-foreground">
                  {activeFiltersCount}
                </span>
              )}
            </Button>
            <Button variant="outline" size="sm">
              <Download className="mr-2 h-4 w-4" />
              Exportar
            </Button>
          </div>
        </div>

        <TransactionFilters
          open={filtersOpen}
          onOpenChange={setFiltersOpen}
          onApplyFilters={handleApplyFilters}
          currentFilters={appliedFilters}
        />

        <Tabs value={activeTab} onValueChange={handleTabChange} className="w-full">
          <div className="flex items-center justify-between">
            <TabsList>
              <TabsTrigger value="all">Todas</TabsTrigger>
              <TabsTrigger value="receitas">Receitas</TabsTrigger>
              <TabsTrigger value="despesas">Despesas</TabsTrigger>
            </TabsList>
            
            <div className="relative w-64">
              <Search className="absolute left-2 top-2.5 h-4 w-4 text-muted-foreground" />
              <Input
                placeholder="Buscar transaes..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="pl-8"
              />
            </div>
          </div>

          <TabsContent value={activeTab} className="mt-4">
            <Card>
              <CardHeader>
                <CardTitle>
                  {activeTab === 'all' && 'Todas as Transaes'}
                  {activeTab === 'receitas' && 'Receitas'}
                  {activeTab === 'despesas' && 'Despesas'}
                </CardTitle>
                <CardDescription>
                  {pagination.total} transaes encontradas
                </CardDescription>
              </CardHeader>
              <CardContent>
                {loading ? (
                  <div className="flex justify-center py-8">
                    <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-primary"></div>
                  </div>
                ) : (
                  <>
                    <Table>
                      <TableHeader>
                        <TableRow>
                          <TableHead>Data</TableHead>
                          <TableHead>Estabelecimento</TableHead>
                          <TableHead>Categoria</TableHead>
                          <TableHead>Tipo</TableHead>
                          <TableHead className="text-right">Valor</TableHead>
                          <TableHead>Marcao</TableHead>
                        </TableRow>
                      </TableHeader>
                      <TableBody>
                        {filteredTransactions.length === 0 ? (
                          <TableRow>
                            <TableCell colSpan={6} className="text-center py-8 text-muted-foreground">
                              Nenhuma transao encontrada
                            </TableCell>
                          </TableRow>
                        ) : (
                          filteredTransactions.map((transaction) => (
                            <TableRow key={transaction.IdTransacao}>
                              <TableCell className="font-medium">
                                {formatDate(transaction.Data)}
                              </TableCell>
                              <TableCell>
                                <div>
                                  <div className="font-medium">{transaction.Estabelecimento}</div>
                                  <div className="text-sm text-muted-foreground">
                                    {transaction.TipoGasto}
                                  </div>
                                </div>
                              </TableCell>
                              <TableCell>
                                <div>
                                  <div className="font-medium">{transaction.GRUPO}</div>
                                  <div className="text-sm text-muted-foreground">
                                    {transaction.SUBGRUPO}
                                  </div>
                                </div>
                              </TableCell>
                              <TableCell>
                                {getTransactionTypeBadge(transaction.TipoTransacao)}
                              </TableCell>
                              <TableCell className="text-right font-medium">
                                {formatCurrency(transaction.ValorPositivo)}
                              </TableCell>
                              <TableCell>
                                {getOrigemBadge(transaction.origem_classificacao)}
                              </TableCell>
                            </TableRow>
                          ))
                        )}
                      </TableBody>
                    </Table>

                    {/* Paginao */}
                    <div className="flex items-center justify-between mt-4">
                      <div className="text-sm text-muted-foreground">
                        Mostrando {Math.min((pagination.page - 1) * pagination.limit + 1, pagination.total)} a{' '}
                        {Math.min(pagination.page * pagination.limit, pagination.total)} de {pagination.total} transaes
                      </div>
                      <div className="flex items-center gap-2">
                        <Button
                          variant="outline"
                          size="sm"
                          onClick={() => handlePageChange(pagination.page - 1)}
                          disabled={pagination.page === 1 || loading}
                        >
                          <ChevronLeft className="h-4 w-4" />
                          Anterior
                        </Button>
                        <div className="text-sm">
                          Pgina {pagination.page} de {pagination.totalPages}
                        </div>
                        <Button
                          variant="outline"
                          size="sm"
                          onClick={() => handlePageChange(pagination.page + 1)}
                          disabled={pagination.page === pagination.totalPages || loading}
                        >
                          Prxima
                          <ChevronRight className="h-4 w-4" />
                        </Button>
                      </div>
                    </div>
                  </>
                )}
              </CardContent>
            </Card>
          </TabsContent>
        </Tabs>
      </div>
    </DashboardLayout>
  )
}